import React, {useState,useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import useFetch from '../../hooks/useFetch';
import useNormalise from './useNormalise';
import {Link} from 'react-router-dom';
import View from './View';
import BtnFiscalYear from '../../components/BtnFiscalYear';
import GrTotal from './GrTotal';
import {actionSetFiscalYear} from '../../redux/fiscalYearSlice';


function Remote() {


  /** fetch jsonapi all sort by "-field_date_on_bank_statement" DESC order */
  const baseurl = "https://yellow-website.com/yw-d9/jsonapi/node/invoice?include=field_bank_account,field_client_id_invoice,field_invoice_details&sort=field_old_invoice_date"


  /** Url for data fetching
   * "fetchAll" or "fetchByFiscalyear"
   */
  const [filter, setFilter] = useState(false);
  console.log("WHICH URL : ", filter);

  /** 
   *  Get Fiscal Year start and end date 
   *  by onClick Button from BtnFiscalYear component.
   *  SELECTED FISCAL YEAR:
  */
const {fiscal_year_start, fiscal_year_end} = useSelector(state => state.set_fiscalyear);
console.log("Fiscal Year: ", fiscal_year_start, fiscal_year_end);

//const dispatch = useDispatch();


/** jsonapi by selected "Fiscal Year" */
const urlwithFilter = `${baseurl}&filter[datefilter][condition][path]=field_old_invoice_date&filter[datefilter][condition][operator]=>=&filter[datefilter][condition][value]=${fiscal_year_start}&filter[filterdate][condition][path]=field_old_invoice_date&filter[filterdate][condition][operator]=<=&filter[filterdate][condition][value]=${fiscal_year_end}`;


/** set the Fiscal Year for generating report */
useEffect(()=>{
  
    fiscal_year_start === '' && fiscal_year_end === '' 
    ? console.log("BIG NULL")
    : console.log("THIS IS NOT NULL")

},[fiscal_year_end, fiscal_year_start])


/** fetch data */
const {data:data, loading:data_loading, error:data_error} = useFetch(filter ? urlwithFilter : baseurl);
const {data:client, loading:client_loading, error:client_error} = useFetch("https://yellow-website.com/yw-d9/jsonapi/node/client_details");
const {data:bank, loading:bank_loading, error:bank_error} = useFetch("https://yellow-website.com/yw-d9/jsonapi/node/bank_account_details");

const {data:dataArr} = useNormalise(data, client, bank);

if(data_loading || client_loading || bank_loading) return <div>Loading...</div>
if(data_error || client_error || bank_error) return console.log(data_error, client_error, bank_error)

return (      
        <div>
        <BtnFiscalYear data={dataArr} />
        </div>
      )
}

export default Remote